<template>
  <div id="app">
    <div class="columns is-gapless">
      <div class="column is-narrow is-hidden-touch animated fadeInLeft">
        <template v-if="loggedIn">
          <menu-logged-in></menu-logged-in>
        </template>
        <template v-else>
          <menu-not-logged-in></menu-not-logged-in>
        </template>
      </div>
      <div class="column">
        <router-view></router-view>
      </div>
      <div class="column is-narrow is-hidden-touch animated fadeInRight">
        <aside class="menu">
          <twitch-chat :channel="Channel" class="twitch-chat"></twitch-chat>
        </aside>
      </div>
    </div>
  </div>
</template>

<script>
import twitchChat from 'vue-twitch-chat';
import MenuNotLoggedIn from './components/MenuNotLoggedIn';
import MenuLoggedIn from './components/MenuLoggedIn';

export default {
  components: {
    twitchChat,
    MenuNotLoggedIn,
    MenuLoggedIn,
  },
  name: 'app',
  data() {
    return {
      Title: 'Octogoin',
      Channel: 'summit1g',
      loggedIn: true,
    };
  },
};
</script>

<style>
html {
  height: 100%;
}

#app {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  background-color: #f9f9f9;
  height: 100%;
}

.level {
  height: 50px;
}

.octogoin-color {
  background-color: #45299f;
}

.octogoin-color-text{
  color: #45299f;
}

.octogoin-logo {
  height: 40px;
}

.level:not(:last-child) {
  margin-bottom: 0;
}

.menu {
  width: 250px;
  border-right: 2px solid #f0f0f0;
  height: 100%;
}

.menu-name {
  white-space: nowrap;
  overflow: hidden;
}

.columns {
  height: 100%;
  margin-bottom: 0px !important;
}

.menu-avatar{
  display: flex;
  justify-content: center;
  padding-top:10px;
}

.menu-avatar img{
  border-radius: 100px;
}

.twitch-chat{
  width: inherit;
  height: inherit;
  border-left: 2px solid #f0f0f0;
}

.is-circle img{
  border-radius: 300px;
}
</style>



